e-block__copied {\n                display: block;\n            }\n\n            .docs-markdown__code-icon {\n                display: none;\n            }\n        }\n    }\n}\n\n.docs-header-link {\n    position: relative;\n\n    & span {\n        position: absolute;\n        display: block;\n        left: 0;\n        top: -60px;\n    }\n}\n\n@mixin docs-markdown-theme($theme) {\n    $primary: map-get($theme, primary);\n    $second: map-get($theme, second);\n    $foreground: map-get($theme, foreground);\n    $is-dark: map-get($theme, is-dark);\n\n    $text: mc-color($foreground, text);\n    $color: mc-color($primary);\n\n    $link: map-get(map-get($theme, components), link);\n    $color_hover: map-get($link, state-hover-text);\n\n    $color_disabled: mc-color($foreground, text-disabled);\n    $fg-default: mc-color($foreground, text);\n    $th-color: mc-color($second, 400);\n    $divider: mc-color($foreground, divider);\n\n    .docs-header-link {\n        color: $text;\n    }\n\n    .docs-markdown__pre .docs-markdown__code,\n    .docs-markdown-code-block {\n        //colors from solarized and Darcula color schemes\n        background: if($is-dark, #2b2b2b, #fdf6e3);\n        color: if($is-dark, #bababa, #657b83);\n    }\n\n    .docs-markdown {\n        color: $fg-default;\n\n        &__a {\n            color: $color;\n            border-bottom: 1px solid rgba($color, 0.5);\n\n            &:focus {\n                outline: none;\n            }\n\n            &:visited {\n                color: $color;\n            }\n\n            &:hover {\n                color: $color_hover;\n                border-bottom-color: rgba($color_hover, 0.5);\n            }\n\n            &.cdk-keyboard-focused {\n                outline: $color solid 2px;\n                outline-offset: 2px;\n            }\n\n            &[disabled] {\n                color: $color_disabled;\n            }\n        }\n\n        &__caption {\n            color: $color_disabled;\n        }\n\n        &__thead {\n            box-shadow: 0 1px 0 $divider;\n        }\n\n        &__th {\n            color: $th-color;\n        }\n\n        &__code-icon {\n            color: $color;\n        }\n    }\n\n\n    .docs-example-viewer__source {\n        position: relative;\n    }\n}\n\n@mixin docs-markdown-typography($config) {\n\n    .docs-header-link {\n        &_3 {\n            @include mc-typography-level-to-styles($config, headline);\n        }\n\n        &_4 {\n            @include mc-typography-level-to-styles($config, body-strong);\n        }\n    }\n\n    .docs-markdown {\n        &__p, &__a, &__tr {\n            @include mc-typography-level-to-styles($config, body);\n        }\n\n        &__caption,\n        &__th {\n            @include mc-typography-level-to-styles($config, caption);\n        }\n    }\n}\n\n\n@mixin docs-linc($color, $color_hover, $color_disabled) {\n    color: $color;\n    border-bottom: 1px solid rgba($color, 0.5);\n\n    &:focus {\n        outline: none;\n    }\n\n    &:visited {\n        color: $color;\n    }\n\n    &:hover {\n        color: $color_hover;\n        border-bottom-color: rgba($color_hover, 0.5);\n    }\n\n    &.cdk-keyboard-focused {\n        outline: $color solid 2px;\n        outline-offset: 2px;\n    }\n\n    &[disabled] {\n        color: $color_disabled;\n    }\n}\n","\n.mc-tab-group {\n    // TODO: need none\n    text-align: left !important;\n}\n","/*\n Darcula color scheme from the JetBrains family of IDEs\n */\n@mixin material-dark(){\n    .hljs {\n        display: block;\n        overflow-x: auto;\n        padding: 0.5em;\n    }\n\n    .hljs,\n    .docs-example-source-copy {\n        background: #2b2b2b;\n        color: #bababa;\n    }\n\n    .hljs-line-numbers {\n        background-color: #313335;\n        color: #485965;\n    }\n\n    .hljs-shadow {\n        position: absolute;\n        bottom: 0;\n        margin: 0;\n        left: 0;\n        height: 44px;\n        width: 100%;\n        opacity: 1;\n        background: linear-gradient(0deg, rgba(#2b2b2b,1) 0%, rgba(#2b2b2b,0.5) 50%, rgba(#2b2b2b,0) 100%);\n\n        &_hidden {\n            opacity: 0;\n        }\n    }\n\n    .hljs-strong,\n    .hljs-emphasis {\n        color: #a8a8a2;\n    }\n\n    .hljs-bullet,\n    .hljs-quote,\n    .hljs-link,\n    .hljs-number,\n    .hljs-regexp,\n    .hljs-literal {\n        color: #6896ba;\n    }\n\n    .hljs-code,\n    .hljs-selector-class {\n        color: #a6e22e;\n    }\n\n    .hljs-emphasis {\n        font-style: italic;\n    }\n\n    .hljs-keyword,\n    .hljs-selector-tag,\n    .hljs-section,\n    .hljs-attribute,\n    .hljs-name,\n    .hljs-variable {\n        color: #cb7832;\n    }\n\n    .hljs-params {\n        color: #b9b9b9;\n    }\n\n    .hljs-string {\n        color: #6a8759;\n    }\n\n    .hljs-subst,\n    .hljs-type,\n    .hljs-built_in,\n    .hljs-builtin-name,\n    .hljs-symbol,\n    .hljs-selector-id,\n    .hljs-selector-attr,\n    .hljs-selector-pseudo,\n    .hljs-template-tag,\n    .hljs-template-variable,\n    .hljs-addition {\n        color: #e0c46c;\n    }\n\n    .hljs-comment,\n    .hljs-deletion,\n    .hljs-meta {\n        color: #7f7f7f;\n    }\n\n}\n","@import 'variables';\n@import '../../../design-tokens/tokens';\n\n\n@function mc-typography-level(\n    $font-size,\n    $line-height: $font-size,\n    $letter-spacing: normal,\n    $font-weight: normal,\n    $font-family: null,\n    $text-transform: null\n) {\n    @return (\n        font-size: $font-size,\n        line-height: $line-height,\n        font-weight: $font-weight,\n        font-family: $font-family,\n        letter-spacing: $letter-spacing,\n        text-transform: $text-transform\n    );\n}\n\n@function _mc-get-type-value($config, $level, $name) {\n    @return map-get(map-get($config, $level), $name);\n}\n\n@function mc-font-size($config, $level) {\n    @return _mc-get-type-value($config, $level, font-size);\n}\n\n@function mc-line-height($config, $level) {\n    @return _mc-get-type-value($config, $level, line-height);\n}\n\n@function mc-font-weight($config, $level) {\n    @return _mc-get-type-value($config, $level, font-weight);\n}\n\n@function mc-letter-spacing($config, $level) {\n    @return _mc-get-type-value($config, $level, letter-spacing);\n}\n\n@function mc-text-transform($config, $level) {\n    @return _mc-get-type-value($config, $level, text-transform);\n}\n\n// Gets the font-family from a typography config and removes the quotes around it.\n@function mc-font-family($config, $level: null) {\n    $font-family: map-get($config, font-family);\n\n    @if $level != null {\n        $font-family: _mc-get-type-value($config, $level, font-family);\n    }\n\n    // Guard against unquoting non-string values, because it's deprecated.\n    @return if(type-of($font-family) == string, unquote($font-family), $font-family);\n}\n\n@mixin mc-typography-level-to-styles($config, $level) {\n    $font-size: mc-font-size($config, $level);\n    $font-weight: mc-font-weight($config, $level);\n    $line-height: mc-line-height($config, $level);\n    $font-family: mc-font-family($config, $level);\n    $text-transform: mc-text-transform($config, $level);\n\n    font: $font-weight #{$font-size}/#{$line-height} $font-family;\n    letter-spacing: mc-letter-spacing($config, $level);\n    text-transform: mc-text-transform($config, $level);\n}\n\n@mixin mc-base-typography($config) {\n    .mc-display-1 {\n        @include mc-typography-level-to-styles($config, display-1);\n    }\n\n    .mc-display-2 {\n        @include mc-typography-level-to-styles($config, display-2);\n    }\n\n    .mc-display-3 {\n        @include mc-typography-level-to-styles($config, display-3);\n    }\n\n    .mc-headline {\n        @include mc-typography-level-to-styles($config, headline);\n    }\n\n    .mc-subheading {\n        @include mc-typography-level-to-styles($config, subheading);\n    }\n\n    .mc-title {\n        @include mc-typography-level-to-styles($config, title);\n    }\n\n    .mc-body {\n        @